<template>
  <div class="mainTemplate_education" style="margin-bottom: 40px">
    <optionTab />

    <div class="mainContainer_education">
      <p class="h1Text">Create a Job Alert Resume</p>

      <div class="previewButton">
        <v-btn
          @click.stop="previewButton"
          color="success"
          class="previewButton__button"
        >
          <v-icon class="previewButton__button__icon">remove_red_eye</v-icon
          >Preview
        </v-btn>
      </div>

      <div>
        <p class="pHeader">
          Education (
          <span>*Required Field of Education</span> )
        </p>
        <v-divider></v-divider>

        <p class="header-text">Education</p>

        <div v-for="(n, i) in educations" :key="i">
          <div class="first-div-close-button">
            <div
              v-if="educations.length > 1"
              class="closeButton"
              @click.stop="() => remove(i)"
            >
              x
              <span class="closeButton__tooltip_text">remove</span>
            </div>
            <div class="ed-row-1">
              <p>Level of Education</p>
              <v-autocomplete
                item-text="title"
                item-value="id"
                v-model="n.exam_id"
                :items="examList"
                outlined
                dense
                background-color="white"
                placeholder="Enter level of Education"
              ></v-autocomplete>
            </div>
          </div>

          <div class="level-of-education">
            <p>Institue Name</p>
            <v-text-field
              background-color="white"
              class="mb-0"
              v-model="n.institute"
              :rules="[(v) => !!v || 'required']"
              placeholder="Institute Name"
              outlined
              dense
            ></v-text-field>
          </div>

          <div class="level-of-education">
            <p>Subject</p>
            <v-text-field
              background-color="white"
              class="mb-0"
              v-model="n.subject"
              :rules="[(v) => !!v || 'required']"
              placeholder="Enter Subject"
              outlined
              dense
            ></v-text-field>
          </div>

          <div class="row-100">
            <div class="row-100-title">
              <p>Time Period</p>
            </div>

            <div class="row-100-1">
              <v-menu
                v-model="n.menu1"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="n.start_year"
                    prepend-inner-icon="event"
                    readonly
                    placeholder="From"
                    backgroundColor="white"
                    outlined
                    dense
                    @keyups="saveData"
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="n.start_year"
                  @input="n.menu1 = false"
                ></v-date-picker>
              </v-menu>
            </div>

            <div class="row-100-3">To</div>

            <div class="row-100-4">
              <v-menu
                v-model="n.menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="n.end_year"
                    prepend-inner-icon="event"
                    readonly
                    placeholder="To"
                    backgroundColor="white"
                    outlined
                    dense
                    @keyups="saveData"
                    v-bind="attrs"
                    v-on="on"
                    :disabled="n.currentlyWorking"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="n.end_year"
                  @input="n.menu2 = false"
                ></v-date-picker>
              </v-menu>
            </div>

            <div class="row-100-6">
              <v-checkbox
                label="I am currently studying here"
                value="A"
                v-model="n.currentlyWorking"
                @change="() => currentlyWorking(n)"
              ></v-checkbox>
            </div>
          </div>

          <div class="level-of-education">
            <p>Result</p>
            <v-text-field
              background-color="white"
              class="mb-0"
              :rules="[(v) => !!v || 'required']"
              placeholder="Result"
              v-model="n.result"
              outlined
              dense
            ></v-text-field>
          </div>

          <!--  <div class="row-we-remove" v-if="educations.length > 1">
            <v-btn
              color="error"
              class="ml-5 mb-3 row-we-remove__btn"
              @click.stop="()=>remove(i)"
            >Remove</v-btn>
          </div>-->
        </div>

        <v-divider></v-divider>
        <v-btn class="ml-5" color="primary" @click.stop="() => addMore()"
          >Add More+</v-btn
        >
      </div>
      <div class="row-14">
        <div class="item-1">
          <v-btn
            @click.stop="
              () => {
                $router.history.push('/biodata');
              }
            "
            >Back</v-btn
          >
        </div>

        <div class="item-2">
          <v-btn
            :loading="loading"
            color="#365899"
            class="white--text"
            @click.stop="nextBtn"
            >Save</v-btn
          >
        </div>
      </div>
    </div>
    <resumePreview
      @close="() => myDialogClose()"
      :dialogVisible="dialogSwitch"
    />

    <!-- loading data  starts-->
    <v-dialog v-model="loadingData" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          Loading Data...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <!-- loading data  ends-->
  </div>
</template>

<script>
import "../../../sass/job-alart/_Education.scss";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import "../../../sass/job-alart/_Resume.scss";
import axios from "axios";
export default {
  name: "Education",
  components: {
    optionTab: () => import("./tab/optionTab"),
    resumePreview: () => import("../Dialog/resumePreview"),
  },
  data: () => {
    return {
      loading: false,
      search: "",
      editor: ClassicEditor,
      editorData: "<p>Content of the editor.</p>",
      editorConfig: {
        // The configuration of the editor.
        height: 500,
      },

      loadingData: false,

      dialogSwitch: false,
      certifates: [
        {
          name: "",
          validity: "",
        },
      ],

      examList: [],

      educations: [
        {
          id: "",
          exam_id: "",
          subject: "",
          menu1: false,
          menu2: false,
          institute: "",
          result: "",
          start_year: "",
          end_year: "",
          details: "",
        },
      ],
      resume: {},
      trainings: [{}],

      certificate_validity: "",

      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu2: false,
    };
  },
  methods: {
    previewButton() {
      // this.dialogSwitch = true;
      if (
        this.R.isNil(this.$store.getters.resume.payload.biodata) ||
        this.R.isEmpty(this.$store.getters.resume.payload.biodata) ||
        this.R.isNil(this.$store.getters.resume.payload.experiences) ||
        this.R.isEmpty(this.$store.getters.resume.payload.experiences) ||
        this.R.isNil(this.$store.getters.resume.payload.qualification) ||
        this.R.isEmpty(this.$store.getters.resume.payload.qualification)
      ) {
        this.$awn.alert("Sorry, Your resume is not complete!");
        return;
      }
      this.dialogSwitch = true;
    },

    myDialogClose() {
      this.dialogSwitch = false;
    },
    getData() {
      this.loadingData = true;
      const headers = {
        Authorization: "Bearer " + this.$cookies.get("accessToken"),
        "Content-Type": "application/json",
        Accept: "application/json",
      };

      axios({
        url: "resume/",
        method: "get",
        data: {},
        headers,
        baseURL: this.$store.state.apiBase,
      })
        .then((response) => {
          console.log(response);
          this.examList = response.data.data.examList;
          this.resume = response.data.data;
          this.educations =
            this.R.isNil(response.data.data.qualification) ||
            this.R.isEmpty(response.data.data.qualification)
              ? this.educations
              : response.data.data.qualification.map((n) => {
                  return { ...n, ...{ exam_id: n.exam.id } };
                });

          console.log("educations....", this.educations);
        })
        .catch((error) => {
          if (error.response.status == 401) {
            axios({
              method: "get",
              baseURL: this.$store.state.apiBase,
              url: `users/new-access-token`,
              params: {
                access_token: this.$cookies.get("accessToken"),
                ip: this.$store.getters.userIp,
              },
              headers,
            })
              .then((response) => {
                console.log("this is refresh token.....", response);
                this.$cookies.set("accessToken", response.data.access_token);
                this.getData();
              })
              .catch((error) => {
                this.$awn.alert("Failed!");
                this.$router.history.push("/signin");
                console.log(error);
                return;
              })
              .finally(() => {});
          }
        })
        .finally(() => {
          if (
            this.R.isNil(this.resume.biodata) ||
            this.R.isEmpty(this.resume.biodata) ||
            this.R.isNil(this.resume.experiences) ||
            this.R.isEmpty(this.resume.experiences)
          ) {
            this.$awn.alert("Your biodata/experices is not complete");
            this.$router.history.push("/biodata");
          }
          this.loadingData = false;
        });
    },
    currentlyWorking(n) {
      console.log("working here", n);
      n.end_year = null;
    },

    nextBtn() {
      if (!this.$refs.form.validate()) {
        this.$awn.alert("Your form is not completed");
        return;
      }

      console.log(this.educations);
      this.loading = true;

      this.$store
        .dispatch("callApi", {
          url: "resume/qualification",
          method: "post",
          data: { qualification: this.educations },
        })
        .then((response) => {
          console.log("resume ... ff ", response);
          this.$awn.success("Updated!");
          setTimeout(() => {
            this.$router.history.push("/");
          }, 1000);

          // this.$refs.form.reset();
          //saves the items from the database in the table
          //  console.log(response);
          //  this.items = response.data;
        })
        .catch(() => {
          this.$awn.alert("Failed!");
          //   this.$awn.alert("Failed");
        })
        .finally(() => {
          this.loading = false;
          //  this.tableLoading = false;
        });
    },
    removeItem(index) {
      console.log(index);
    },
    saveData() {},
    addMore() {
      this.educations.push({
        id: "",
        subject: "",
        institute: "",
        result: "",
        menu1: false,
        menu2: false,
        start_year: "",
        end_year: "",
        details: "",
      });
    },
    remove(index) {
      this.educations.splice(index, 1);
    },
  },
  mounted() {
    this.$store.commit("resumePrevbtn", false);
    this.$store.commit("componentName", "Education");

    this.getData();
  },
};
</script>
